<template>
  <div>
    <div class="page-title">
      <h3>Навигация</h3>
      <button class="btn waves-effect waves-light btn-small">
        <i class="material-icons">refresh</i>
      </button>
    </div>

    <table>
      <thead>
      <tr>
        <th>Название</th>
        <th>Активная страница</th>
      </tr>
      </thead>
      <tbody>
        <tr v-for="item in this.$store.state.sidebar.pages"
            :key="item.title"
        >
            <td>{{item.title}}<input :data-url="item.url" v-model="title" type="text"></td>
            <td>
              <div class="switch">
                <label>
                  Выкл.
                  <input :data-url="item.url" type="checkbox" @change="onChange($event)" :checked="item.show">
                  <span class="lever"></span>
                  Вкл.
                </label>
              </div>
            </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
// import {mapActions, mapGetters} from 'vuex'

export default {
  name: "navigations",
  data: () => ({
    title: ''
  }),
  methods: {
    async onChange(e) {
      const response = await this.$store.dispatch('updateDataNavigations', {
        url: e.target.dataset.url,
        show: e.target.checked
      })
      this.$store.state.sidebar.pages = response.data.pages
      console.log(response)
    }
  }
}
</script>

